@import url(https://fonts.googleapis.com/css?family=Kaushan+Script|Roboto&display=swap);
body, html {
  height: 100%;
  margin: 0 !important;
  padding: 0 !important;
  font-family: Roboto, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.cursor-pointer{
  cursor: pointer;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
button {
  outline:none !important;
}
a {
  text-decoration: none;
}
.home{
  padding-top: 5rem;
  height: 100vh;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #F9F9F9;
  
}
.form, .box-register {
  max-width: 25rem;
  margin: 0 auto;
  border-radius: 0.2rem;
  background-color: #ffffff;
  padding: 0.5rem 2rem 1rem 2rem;
  margin-top: 1rem;
  text-align:center;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 4px 12px;
}
.box-register{
  padding: 1.2rem 2rem;
}
.form .title{
  font-size: 1.3rem;
}
.form > div{
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.form > div > div{
border-bottom: 1px solid #838383;
}
.form > div > span{
  margin-top: 0.5rem;
  text-align: right;
  font-size: 0.9rem;
}

.form > div > div > i{
  margin-right: 0.5rem;
  color: #838383;
}

.form div:last-child{
  text-align: center;
}
.form div:last-child a {
  color: #535353;
}

.form input {
  outline: none;
  border: 0.07rem solid #A3A3A3;
  border-radius: 0.2rem;
  font-family: Roboto, serif;
  padding: 0.5rem;
  font-size: .8rem;
  font-weight: 400;
  background-color: rgb(253, 252, 252);
}
.form-modal .btn{
  margin-top: 1.5rem;
}
.btn {
  font-family: Roboto, serif;
  padding: 0.6rem;
  border-radius: 0.3rem;
  border: none;
  box-shadow: none;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
}
.btn:hover{
  box-shadow:none;
}
.btn.primary {
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  background-color: none;
  color: #8F8F8F !important;
}
.btn.primary:hover{
  color: #000000 !important;
}

.btn.secundary{
  background: #1200BA;
}

.btn.danger{
  background: #F42F2F;
}
.btn.danger:hover{
  background: #E11A1A;
}
.modal-title{
  margin-top: 1rem;
  font-size: 1.6rem;
  text-align:center;
}
.form-modal .select{
  text-decoration: none;
  border: none;
  width: 90%;
  box-shadow: none;
}
.new-category{
  color: #9888e2;
  cursor: pointer;
  font-size: 1.1rem;
}
/* Home screen */
.home-screen .title{
  font-family: Roboto, serif;
  padding: 0.5rem;
  font-size:1.5rem;
}
/* datatable */
.mdb-datatable{
  position: relative !important;
  width: 99% !important;
}
.justify-content-end {
  margin-left: 0.5rem;
  justify-content: left !important;
}

.data-info{
  display:flex;
  justify-content:space-between;
  flex-wrap: wrap;
  padding: 0.5rem;
}
.data-card{
  display: block;
  border: none;
  max-width: 40rem;
  width: 40rem;
  margin-top: 0.5rem;
  padding: 0.5rem;
  border-radius: 0.2rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  text-align: left;
}
.data-content{
  display: none;
  padding:1rem;
  padding-bottom: 5.5rem;
}
.data-table-items {
  margin-top: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  border-radius: 0.3rem;
  font-family: Roboto, serif;
  display: flex;
  align-items: center;
}
.data-table-items > div{
  padding: 0.3rem;
}
.data-table-items > div:nth-child(1){
  background-color: #080058;
  color: #ffffff;
  padding: 0.3rem 0.9rem;
  border-top-left-radius: 0.3rem;
  border-bottom-left-radius: 0.3rem;

}
.data-table-items > div:nth-child(3){
  margin-left: auto;
  font-weight: 500;
}
.data-date > div:nth-child(1){
  font-size: 0.6rem;
}
.data-date > div:nth-child(2){
  font-size: 1.3em;
}
.subcategory{
  font-weight: 300;
  font-size: 0.8rem;
}

.data-card div:first-child{
font-size: 0.7rem;
}
.data-card div:last-child{
  font-size: 1.2rem;
  font-weight: 500;
  }

.data-table{
  margin: 0.5rem;
  padding-top: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.selects-datable{
  padding: 0.5rem;
}

.selects-datable .select{
  max-width: 40rem;
  margin-top: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 1px 4px;
}
table.table td:nth-child(3){
  font-weight: bold;
}

.row {
  padding-left: 0.6rem;
  position: relative !important;
  width: 100%;
}
/* Modal */
.modal {
  margin-top: 5rem;
}

/* form modal */
.form-modal {
  max-width: 30rem;
  margin: 0 auto;
  background-color: #ffffff;
  padding: 0.5rem 0.5rem;
}
.form-title{
  font-size: 1.5rem;
  text-align: center;
}
.form-title > div{
  padding: .5rem;
}
.form-modal > div{
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.form-modal > div > div{
  position: relative;
}

.form-modal > div > div > svg{
  position: absolute;
  top: 0.5rem;
  right: 0;
  width: 2.5rem;
  font-weight: boldsub-form;
  cursor: pointer;
}
.form-modal label {
  font-family: Roboto, serif;
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 0.2rem;
}
.form-modal > div > input, .form-modal > div > textarea {
  border: none;
  outline: none;
  font-family: Roboto, serif;
  font-size: 1rem;
  padding: 0.6rem;
  border-radius: 0.2rem;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

}

/* sub-form */
.sub-form{
  display: flex;
  flex-direction: row;
  width: 90%;
  margin-top: 1rem;
}
.sub-form > div > svg {
  cursor: pointer;
  margin-top: 0.4rem;
  margin-left: 0.5rem;
}
.sub-form > div > input{
  outline: none;
  padding: 0.4rem;
  border: none;
  border-radius: 0.2rem;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}
.sub-form .btn{
  width: 5rem;
}

/* alert */
.alert{
  border-radius: 0.2rem;
  padding: 0.4rem 1rem;
  font-size: 1rem;
}
.alert-info{
  background-color: #BCBFFF;
  color: #7D8300;
}
.alert-danger{
  color: red;
  background-color: #00D5D4;
}

/* Header */

/* Style the header with a grey background and some padding */
.header {
  overflow: hidden;
  background-color: #f1f1f1;
}

/* Style the header links */
.header a {
  float: left;
  color: black;
  text-align: center;
  /* padding: 12px; */
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
}

/* Change the background color on mouse-over */
.header a:hover {
  background-color: #ddd;
  color: black;
}

/* Style the active/current link*/
.header a.active {
  background-color: #2B15FF;
  color: white;
}

/* Float the link section to the right */
.header-right {
  margin-top: 1.2rem;
  float: right;
}
.header-right a{
  padding: 0.4rem;
}

.logo{
  padding: 0;
  width: 6rem;
  height: 5rem;
}
.MuiAppBar-colorPrimary {
  background-color: #080058 !important;
}

/* spinner */
.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border-left-color: #09f;
  margin: 0 auto;
  -webkit-animation: spin 1s ease infinite;
          animation: spin 1s ease infinite;
  vertical-align: middle;
  margin-top: 1%;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* floating-button */
.floating-button{
  bottom: 3%;
  right: 3%;
  position: fixed;
}
.floating-button > button{
  cursor: pointer;
  background: #080058;
  border: none;
  width: 3rem;
  height: 3rem;
  margin: 0;
  padding: 0;
  border-radius: 5rem;
}
.floating-button > button > svg{
  font-size: 2.5rem;
  color: #ffffff;
}

.container, .container-table {
  max-width: 60rem;
  margin: 0 auto;
}
.MuiDrawer-paper{
  width: 50%;
}
.drawer-header{
  border-bottom: 1.5px solid #e9d2d2;
  margin: 1rem 1.3rem;
  /* padding: 1rem; */
}
.drawer-body{
  height: 100vh;
}
.drawer-back{
  cursor: pointer;
  margin-bottom: 1rem;
}

h2 {
  font-size: 26px;
  margin: 20px 0;
  text-align: center;
  /* small {
    font-size: 0.5em;
  } */
}

.responsive-table {
  padding: 0;
  margin: 0;
  margin-top: 1rem;
  /* width: 100%; */
}
  li {
    border-radius: 3px;
    /* padding: 25px 30px; */
    padding: 1rem 0.5rem;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
    /* width: 100%;
    position: relative; */
  }
  .table-header {
    background-color: #080058;
    color: #ffffff;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.03em;
    /* width: 100%;
    position: relative; */
  }
  .table-row {
    background-color: #ffffff;
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.1);
  }
  .col-1 {
    flex-basis: 10%;
  }
  .col-2 {
    flex-basis: 40%;
  }
  .col-3 {
    flex-basis: 25%;
  }
  .col-4 {
    flex-basis: 25%;
  }
/* } */

@media only screen and (max-width: 450px) {
  .home {
    padding: 0;
    margin-top: 5rem;
  }
  .form, .box-register {
    max-width: 100%;
    margin: 0 auto;
    border: none;
    border-radius: 0.2rem;
    background-color: #ffffff;
    padding: 0.5rem 1rem 1rem 1rem;
    margin-top: 1rem;
    text-align:center;
    box-shadow: none;
  }
  .modal, .modal-dialog, .modal-body {
    margin: 0;
    padding: 0 !important;
  }
  .modal-body{
    height: 75vh;
    position: relative;
  }
  .modal-body .form-modal{
    position: absolute;
    width: 100%;
  }
  .modal-content{
    margin-top: 4rem;
    width: 100%;
  }
  .mdb-datatable div:nth-child(2){
    width: 450px;
    overflow-x: scroll;
  }
  .data-content{
    display: block;
  }
  .data-table{
    display: none;
    max-width: 450px;
    overflow-x: scroll;
  }
}

@media only screen and (max-width: 540px) {
  .container-table{
    max-width: 540px;
    overflow-x: scroll;
    margin: 0.2rem;
  }
  .responsive-table{
    width: 540px;
  }
}

@media only screen and (max-width: 700px) {
  .MuiDrawer-paper{
    width: 100%;
  }
}


